
@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@1,200&family=Pirata+One&display=swap');
* {
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: 'Nunito', sans-serif;
    box-sizing: border-box;
}
body {
    background-color: #eff3f7;
    height: 100%;
}
html,
body,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}
.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 70px;
}
