@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@1,200&family=Pirata+One&display=swap);

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    width: 90%;
    padding-top: 70px;
}

.movies .movie {
    width: 40%;
    background-color: #ffffff;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #b2b3bc;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 
    0 8px 16px -8px rgba(0,0,0,0.3),
    0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 180px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0,0,0,0.3),
    0 -12px 36px -8px rgba(0,0,00.025);
}

.movie .movie_title,
.movie .movie_year  {
    margin: 0;
    font-weight: 300;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres {
    display: flex;
    margin: 5px 0px;
}
.movie_genres li {
    margin-right: 10px;
    font-size: 14px;
}

* {
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: 'Nunito', sans-serif;
    box-sizing: border-box;
}
body {
    background-color: #eff3f7;
    height: 100%;
}
html,
body,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}
.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 70px;
}

